@import url(https://fonts.googleapis.com/css2?family=Slabo+27px&display=swap);
*{

  margin:0;
  padding:0;
  border:0;
  box-sizing: border-box;
  
}

body{
  font-family: 'Slabo 27px', serif;
}

.container{


  max-width:800px;
  margin: 0 auto;

}
#informacion{
  display: flex;
  max-width: 800px;
  width: 100%;

}

p{
  text-align: justify;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header{
    background-image: url(/HojaDeVida/static/media/header.85398ad4.png);
    background-repeat: no-repeat ;
    display:flex;
    padding: 40px;
}

.foto{
    flex-basis: 40%;
    text-align:center;
}


.foto img{
    max-width: 200px;
border-radius: 50%;
border: 5px solid #ffffff;

}
.texto{
    flex-basis: 60%;
}

h1,h3{

    color:#ffffff;
}

.texto h3{
    font-weight: 200;
}
.contacto{
    border-right: 3px solid #0c448d6b;
    padding:10px 30px;
    flex-basis: 50%;
    padding-left: 20px;
  }
.info{
    margin-bottom: 5px;
}
 
.info i{
    background-color: #889FD6;
    padding: 10px;
    border-radius: 50%;
    margin-right: 10px;
    font-size: 20px;


}
.enlaces img{
   max-width: 150px; 
}


h2{
    text-align: center;
}


.acerca{
   flex-basis: 50%;
  }


.infolab{
    padding: 10px 30px;
}

.titlab{
    background-color: #9CA8CA;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 5px 20px;
    margin: 10px 0;
}
.estudio p{
    padding-left: 40px;
    margin: 10px 0;
}

progress[value]{

    width: 100%;
    height: 15px;
    -webkit-appearance: none;
            appearance: none;
    color: #9CA8CA;
    
}

progress::-moz-progress-bar {background-color:#9CA8CA;}
progress::-webkit-progress-bar{background-color:#9CA8CA;}
progress[value]::-webkit-progress-value {
    background-color: #3a4e7a;
}

.habilidades{
    padding-left: 40px;
    margin: 10px 0;
}
footer{
    background-image: url(data:image/png;base64,);
    min-height: 200px;
}
